.Post {
    padding: 1em;
    text-align: center;
    border: 1px solid #eee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    cursor: pointer;
    color: #CDCDCD;
    min-width: 400px;
    min-height: 175px;
    justify-self: center;
}

.Post:hover {
    background-color: #f2e5dc;
    color: #fa923f;
}



